/* eslint-disable react/no-unescaped-entities */

import React from 'react'
import styled from 'styled-components'
import { connect } from 'react-redux'
import classNames from 'classnames'
import { dublicateUser, deleteUser } from '../../../store/users/action'
import FeatureButton from '../../../components/FeatureButton'
import More from '../../../components/Menu'

import Icon1 from '../../../assets/images/feature-1.svg'
import Icon2 from '../../../assets/images/feature-2.svg'
import Icon3 from '../../../assets/images/feature-3.svg'
import Icon4 from '../../../assets/images/feature-4.svg'
import Icon5 from '../../../assets/images/feature-5.svg'
import Icon6 from '../../../assets/images/feature-6.svg'
import Icon7 from '../../../assets/images/feature-7.svg'
import Icon8 from '../../../assets/images/feature-8.svg'
import Icon9 from '../../../assets/images/feature-9.svg'
import SIP from '../../../assets/images/sip.svg'
import Cell from '../../../assets/images/mobile.svg'

const Wrapper = styled.div`
  table {
    width: 100%;
    border-collapse: collapse;
    border-spacing: 0;

    th,
    td {
      padding: 0;
    }

    td {
      background: #fff;
    }
  }
  thead th > div {
    height: 60px;
    line-height: 60px;
    text-align: center;
    background-color: #e8e8e8;
    color: #888;
    font-family: 'Open Sans';
    font-size: 1.25em;
    font-weight: 400;

    &.bold-th {
      font-weight: 700;
    }
  }

  tbody td > div:not(.features-collapse) {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 87px;
    background: #fff;
    font-family: 'Open Sans';
    font-size: 1.25em;
    font-weight: 400;
    color: #888;

    &::after {
      content: '';
      position: absolute;
      top: 50%;
      right: 0;
      transform: translate(0, -50%);
      width: 1px;
      height: 60%;
      background: rgba(204, 204, 204, 0.4);
    }
  }

  .user-letter {
    display: inline-block;
    width: 46px;
    height: 46px;
    line-height: 46px;
    text-align: center;
    border-radius: 50%;
    background: #464646;
    color: #fff;
    font-family: Roboto;
    font-size: 1.25em;
    font-weight: 700;
    text-transform: uppercase;
  }

  .user-features {
    cursor: pointer;

    .icon {
      display: none;
      max-width: 35px;
      max-height: 33px;
      margin: 5px;

      &.active {
        display: inline-block;
      }
    }
  }

  .more-menu {
    position: absolute;
    right: 30px;
    font-size: 0.95rem;
    color: #000;
  }

  .user-name {
    margin: 0 0 0 25px;
  }

  .more {
    cursor: pointer;
    color: #888;
    font-size: 2.5em;
    user-select: none;

    span {
      transform: translate(10px, 0) rotate(90deg);
    }
  }

  .td-name-container {
    position: relative !important;
    justify-content: flex-start !important;
    padding: 0 0 0 40px !important;
  }

  .expand {
    position: absolute;
    top: 50%;
    left: 4px;
    transform: translate(0, -50%);
    cursor: pointer;
    border: 0;
    background: none;
    outline: none;

    i {
      transition: transform 0.3s;
    }

    &.active i {
      transform: rotate(90deg);
    }
  }

  .features-collapse {
    position: relative;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    max-width: 900px;
    width: 100%;
    max-height: 0;
    margin: 0 auto;
    opacity: 0;
    transition: max-height 0.3s, opacity 0.3s;
    overflow: hidden;
    pointer-events: none;

    &.active {
      opacity: 1;
      max-height: 700px;
      pointer-events: initial;
    }

    > div {
      margin: 0 0 18px;
    }
  }
`

const getLetters = (firstName, lastName) =>
  `${(firstName || '').substr(0, 1)}${(lastName || '').substr(0, 1)}`
const getFullName = (firstName, lastName) => `${firstName || ''} ${lastName || ''}`

class TableContainer extends React.PureComponent {
  state = {
    expandedId: null,
    openedMenuId: null
  }

  componentDidMount() {
    window.addEventListener('click', this.listener)
  }

  componentWillUnmount() {
    window.removeEventListener('click', this.listener)
  }

  listener = e => {
    if (!e.target.closest('.more')) this.setState({ openedMenuId: null })

    return true
  }

  handleChoose = userId => actionId => {
    this.setState({ openedMenuId: null }, () => {
      if (actionId === 0) this.props.dublicateUser(userId)
      if (actionId === 1) this.props.deleteUser(userId)
    })
  }

  render() {
    const { className = '', users, onFeaturesClick, onFeatureClick } = this.props

    const { expandedId, openedMenuId } = this.state

    return (
      <Wrapper className={className}>
        <table>
          <thead>
            <tr>
              <th>
                <div className="bold-th">User's Settings</div>
              </th>
              <th>
                <div>Extensions</div>
              </th>
              <th>
                <div>Phone Numbers</div>
              </th>
              <th>
                <div>Devices</div>
              </th>
              <th>
                <div>User Features</div>
              </th>
              <th>
                <div />
              </th>
            </tr>
          </thead>
          <tbody>
            {users.map((user, index) => (
              <React.Fragment key={index.toString()}>
                <tr>
                  <td>
                    <div className="td-name-container">
                      <button
                        className={classNames('expand', { active: expandedId === user.id })}
                        onClick={() =>
                          this.setState(p => ({
                            expandedId: p.expandedId === user.id ? null : user.id
                          }))
                        }
                      >
                        <i className="material-icons">arrow_right</i>
                      </button>
                      <span className="user-letter">
                        {getLetters(user.firstName, user.lastName)}
                      </span>
                      <span className="user-name">
                        {getFullName(user.firstName, user.lastName)}
                      </span>
                    </div>
                  </td>
                  <td>
                    <div className="user-extensions">{user.extensionNumber || 'None'}</div>
                  </td>
                  <td>
                    <div className="user-phone-numbers">
                      {user.directPhone ? `+${user.directPhone}` : 'None'}
                    </div>
                  </td>
                  <td>
                    <div className="user-devices">
                      {user.devices ? (
                        <>
                          <img
                            style={{
                              margin: '0 10px',
                              width: '37px',
                              height: '37px',
                              display: user.devices[1] ? 'block' : 'none'
                            }}
                            src={SIP}
                            alt="SIP"
                          />
                          <img
                            style={{
                              margin: '0 10px',
                              width: '37px',
                              height: '37px',
                              display: user.devices[2] ? 'block' : 'none'
                            }}
                            src={Cell}
                            alt="Cell"
                          />
                        </>
                      ) : (
                        'None'
                      )}
                    </div>
                  </td>
                  <td>
                    <div
                      className="user-features flex justify-center align-center"
                      role="button"
                      onClick={() => {
                        onFeaturesClick(user.id)
                      }}
                    >
                      <img
                        className={classNames('icon', { active: (user.features || {})[2] })}
                        src={Icon2}
                        alt="feature"
                      />
                      <img
                        className={classNames('icon', { active: (user.features || {})[4] })}
                        src={Icon4}
                        alt="feature"
                      />
                      <img
                        className={classNames('icon', { active: (user.features || {})[8] })}
                        src={Icon8}
                        alt="feature"
                      />
                    </div>
                  </td>
                  <td>
                    <More
                      id={user.id}
                      onMoreClick={() =>
                        this.setState({
                          openedMenuId: openedMenuId === user.id ? null : user.id
                        })
                      }
                      className="more-menu"
                      isOpen={openedMenuId === user.id}
                      onSelect={this.handleChoose(user.id)}
                      items={['Duplicate', 'Delete']}
                    />
                  </td>
                </tr>
                <tr>
                  <td colSpan="6">
                    <div
                      className={classNames('features-collapse', {
                        active: expandedId === user.id
                      })}
                    >
                      <FeatureButton
                        isChecked={(user.features && user.features[1]) || false}
                        label="Caller ID Number"
                        icon={Icon1}
                        onClick={onFeatureClick(user, 1)}
                      />
                      <FeatureButton
                        isChecked={(user.features && user.features[2]) || false}
                        label="Call Forward"
                        icon={Icon2}
                        onClick={onFeatureClick(user, 2)}
                      />
                      <FeatureButton
                        isChecked={(user.features && user.features[3]) || false}
                        label="Hot-Desking"
                        icon={Icon3}
                        onClick={onFeatureClick(user, 3)}
                      />
                      <FeatureButton
                        isChecked={(user.features && user.features[4]) || false}
                        label="Voicemail Box"
                        icon={Icon4}
                        onClick={onFeatureClick(user, 4)}
                      />
                      <FeatureButton
                        isChecked={(user.features && user.features[5]) || false}
                        label="Callback Sevice"
                        icon={Icon5}
                        onClick={onFeatureClick(user, 5)}
                      />
                      <FeatureButton
                        isChecked={(user.features && user.features[6]) || false}
                        label="Missed Call Alert"
                        icon={Icon6}
                        onClick={onFeatureClick(user, 6)}
                      />
                      <FeatureButton
                        isChecked={(user.features && user.features[7]) || false}
                        label="Find me, Follow me"
                        icon={Icon7}
                        onClick={onFeatureClick(user, 7)}
                      />
                      <FeatureButton
                        isChecked={(user.features && user.features[8]) || false}
                        label="Music-On-Hold"
                        icon={Icon8}
                        onClick={onFeatureClick(user, 8)}
                      />
                      <FeatureButton
                        isChecked={(user.features && user.features[9]) || false}
                        label="Customized Call Recording"
                        icon={Icon9}
                        onClick={onFeatureClick(user, 9)}
                      />
                    </div>
                  </td>
                </tr>
              </React.Fragment>
            ))}
          </tbody>
        </table>
      </Wrapper>
    )
  }
}

export default connect(
  ({ users }) => ({ users: users.users }),
  { dublicateUser, deleteUser }
)(TableContainer)
