/** @flow */

import React from 'react'
import { connect } from 'react-redux'
import { IntlProvider } from 'react-intl'
import ReactGA from 'react-ga'
import { withRouter } from 'react-router-dom'
import type { RouterHistory, Location } from 'react-router-dom'
import { formattedMessages } from './i18n'
import ROUTES from './pages/routes'
import Router from './pages'

ReactGA.initialize('UA-130877148-2')

type Props = {
  locale: string,
  location: Location,
  history: RouterHistory
}

class App extends React.PureComponent {
  componentDidMount() {
    const { pathname, search } = this.props.location
    ReactGA.pageview(pathname + search)
  }

  componentDidUpdate({ location: prevLocation }) {
    const { pathname: prevPath, search: prevSearch } = prevLocation
    const { pathname: nextPath, search: nextSearch } = this.props.location

    if ((prevPath !== nextPath || prevSearch !== nextSearch) && nextPath !== ROUTES.dashboard) {
      ReactGA.pageview(nextPath + nextSearch)
    }
  }

  render() {
    const { locale } = this.props

    return (
      <IntlProvider
        key={locale}
        locale={locale}
        messages={formattedMessages[locale]}
        defaultLocale="en"
      >
        <>
          <Router />
        </>
      </IntlProvider>
    )
  }
}

type MapStateToProps = (state: Object) => Props

const mstp: MapStateToProps = ({ i18n }) => ({ locale: i18n.locale })

export default connect(mstp)(withRouter(App))
