import React from 'react'
import styled from 'styled-components'

const Wrapper = styled.div`
  width: 120px;
  padding: 8px 0;
  border-radius: 4px;
  background: #fff;
  z-index: 999999;
  box-shadow: 0px 5px 5px -3px rgba(0, 0, 0, 0.2), 0px 8px 10px 1px rgba(0, 0, 0, 0.14),
    0px 3px 14px 2px rgba(0, 0, 0, 0.12);

  ul {
    padding: 0;
  }

  ul > li {
    display: flex;
    align-items: center;
    list-style: none;
    height: 46px;
    padding: 11px 16px;
    box-sizing: border-box;
    white-space: nowrap;
    cursor: pointer;
    font-size: 1em;

    &:hover {
      background: rgba(0, 0, 0, 0.08);
    }
  }
`

class Menu extends React.PureComponent {
  render() {
    const { isOpen, onSelect, items, onMoreClick, className } = this.props

    return (
      <div id={`${this.props.id} more`} className="more" onClick={onMoreClick}>
        <span>...</span>
        {isOpen && (
          <Wrapper className={className}>
            <ul>
              {items.map((item, index) => (
                <li key={index.toString()} onClick={() => onSelect(index)}>
                  {item}
                </li>
              ))}
            </ul>
          </Wrapper>
        )}
      </div>
    )
  }
}

export default Menu
