/** @flow */

import React from 'react'
import styled from 'styled-components'

const Wrapper = styled.div`
  margin: 200px 0 20px 200px;
  max-width: 120px;
  width: 100%;
  padding: 8px 0;
  border-radius: 4px;
  box-shadow: 0px 5px 5px -3px rgba(0, 0, 0, 0.2), 0px 8px 10px 1px rgba(0, 0, 0, 0.14),
    0px 3px 14px 2px rgba(0, 0, 0, 0.12);

  ul {
    padding: 0;
  }

  ul > li {
    display: flex;
    align-items: center;
    list-style: none;
    height: 46px;
    padding: 11px 16px;
    box-sizing: border-box;
    white-space: nowrap;
    cursor: pointer;

    &:hover {
      background: rgba(0, 0, 0, 0.08);
    }
  }
`

class Sandbox extends React.PureComponent<*, *> {
  state = {
    open: false
  }

  toggle = () => this.setState(p => ({ open: !p.open }))

  render() {
    const { open } = this.state

    return (
      <>
        <button onClick={this.toggle}>open</button>
        {open && (
          <Wrapper>
            <ul>
              <li>Rebeca</li>
              <li>Rebeca 12</li>
            </ul>
          </Wrapper>
        )}
      </>
    )
  }
}

export default Sandbox
